.btn {
  background-color: transparent;
  border: none;
  color: $white;
  letter-spacing: 0.2px;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  width: 40px;
  height: 40px;
  padding: 7px 10px 7px 10px;
  transition: background-color 0.3s ease;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

  &:focus {
    outline: none;
  }

  &:hover {
    background-color: darken($color: white, $amount: 20);
  }
}
.btn-notes {
  background-color: transparent;
  border: none;
  color: $white;
  letter-spacing: 0.2px;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  padding: 7px 10px 7px 10px;
  transition: background-color 0.3s ease;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);

  &:focus {
    outline: none;
  }

  &:hover {
    background-color: darken($color: white, $amount: 20);
  }
}

.btn-primary {
  background-color: $primary;
  border-radius: 2px;

  &:disabled {
    background-color: lighten($color: $primary, $amount: 15);
  }

  &:hover {
    background-color: darken($color: $primary, $amount: 5);
  }
}

.btn-block {
  width: 100%;
}

.btn-danger {
  width: 100%;
  background-color: red;
  font-size: 16px;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: darken($color: red, $amount: 10);
  }
}

