.facebook-btn {
  display: flex;
  justify-content: center;
  width: 70%;
  color: white;
  background: #3b5998;
  cursor: pointer;
  padding: 5px 15px;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  transition: box-shadow 0.3s ease;
  font-size: 12px;

  &:hover {
    box-shadow: 0 0 6px $google-blue;
    background-color: darken($color: #3b5998, $amount: 5);
  }
  &:active {
    background: $button-active-blue;
  }
}

.google-btn {
  width: 70%;
  display: flex;
  justify-content: center;
  color: white;
  background: #db4a39;
  cursor: pointer;
  padding: 5px 15px;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  transition: box-shadow 0.3s ease;
  font-size: 12px;

  &:hover {
    box-shadow: 0 0 6px $google-blue;
    background-color: darken($color: #db4a39, $amount: 5);
  }
  &:active {
    background: $button-active-blue;
  }
}
